$mozBackCart: -moz-radial-gradient(0 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
              -moz-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
              -moz-radial-gradient(100% 0, circle, rgba(204,0,0,0) 39px, #fff 40px),
              -moz-radial-gradient(0 0, circle, rgba(204,0,0,0) 39px, #fff 40px);
$oBackCart: -o-radial-gradient(0 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -o-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -o-radial-gradient(100% 0, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -o-radial-gradient(0 0, circle, rgba(204,0,0,0) 39px, #fff 40px);
$wBackCart: -webkit-radial-gradient(0 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -webkit-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -webkit-radial-gradient(100% 0, circle, rgba(204,0,0,0) 39px, #fff 40px),
            -webkit-radial-gradient(0 0, circle, rgba(204,0,0,0) 39px, #fff 40px);
$yellow: #f1ac11;
//VARIABLES^^^

section.find-us {
  width: 100%;
  height: 490px;
  position: relative;
  background-image: url("../images/secretsBack.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(1, 1, 1, 0.8);
  }

  .find-us-cart {
    width: 580px;
    height: 330px;
    background: #fff;
    background: $mozBackCart;
    background: $oBackCart;
    background: $wBackCart;
    background-position: bottom left, bottom right, top right, top left;
    -moz-background-size: 50% 50%;
    -webkit-background-size: 50% 50%;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    &::before {
      content: "";
      width: 2px;
      height: 100%;
      position: absolute;
      background-color: #fff;
      top: 0;
      left: 50%;
      margin-left: -1px;
      z-index: 0;
    }
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      position: absolute;
      background-color: #fff;
      top: 50%;
      left: 0;
      margin-top: -1px;
      z-index: 0;
    }
    h2 {
      font-size: 1.7rem;
      font-style: italic;
      text-transform: uppercase;
      text-align: center;
      color: #000;
      margin-top: 79px;
      margin-bottom: 40px;
      position: relative;
      z-index: 1;
    }
    .address {
      width: 100%;
      height: 37px;
      position: relative;
      z-index: 1;

      .logo {
        width: 220px;
        height: 100%;
        position: absolute;
        left: 65px;
        top: 0;

        p.logo-p {
          font-size: 1.2rem;
          color: $yellow;
          text-align: right;
        }

        span.logo-cup {
          display: block;
          width: 25px;
          height: 20px;
          position: absolute;
          left: 50px;
          top: 50%;
          margin-top: -15px;
          border: 3px solid $yellow;
          border-bottom: none;
          border-radius: 50% 50% 0 0;
          &::before {
            content: "";
            position: absolute;
            top: -9px;
            left: 50%;
            margin-left: -3px;
            width: 0;
            height: 0;
            border: 3px solid $yellow;
            border-radius: 50%;
          }
          &::after {
            content: "";
            position: absolute;
            left: -6px;
            bottom: 0;
            width: 31px;
            height: 3px;
            background-color: $yellow;

          }
        }
      }
      p.location {
        display: block;
        width: 140px;
        height: 100%;
        font-size: 1rem;
        color: #000;
        font-style: italic;
        position: absolute;
        top: 0;
        right: 65px;
      }
    }
    button.shaded-btn {
      position: relative;
      display: block;
      margin: 0 auto;
      margin-top: 55px;
      transition: 0.2s;
      font-size: 1rem;
      padding: 10px 12px 9px 13px;
      border: none;
      background-color: $yellow;
      text-transform: uppercase;
      font-style: italic;
      border-radius: 2px;
      box-shadow: 0 3px 0 0 #b47d00;

      &:hover {
        cursor: pointer;
      }
      &:focus {
        margin-top: 58px;
        box-shadow: none;
        border: none;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  section.find-us {
    .find-us-cart {
      width: 95%;
      .address {

        .logo {
          left: 0;
          .logo-p {
            display: none;
          }
        }
        .location {
          right: 10px;
        }
      }
    }
  }
}
